import { useStore } from 'vuex'
import { ref, computed, ComputedRef } from 'vue'
import store from '@/store'

interface LoadParams {
  currentPage: number
  pageSize: number
  [ key: string ]: any
}

// actionName dispatch
// total 
// params
const useLoadMore = (actionName: string, total: ComputedRef<number>, params: LoadParams = { currentPage: 2, pageSize: 5 }) => {
  // store
  const store = useStore()
  // paramscurrentPage
  const currentPage = ref((params && params.currentPage) || 1)
  const requestParams = computed(() => ({
    ...params,
    currentPage: currentPage.value + 1
  }))
  const loadMorePage = () => {
    store.dispatch(actionName, requestParams.value).then(() => {
      currentPage.value++
    })
  }
  // const isLastPage = computed(() => {
  //   return (requestParams.value.pageSize * (currentPage.value - 1)) >= total.value
  // })
  const isLastPage = computed(() => {
    return Math.ceil((total.value || 1) / params.pageSize) === currentPage.value
  })
  return {
    loadMorePage,
    isLastPage
  }
}

export default useLoadMore
