<template>
  <div class="edit-profile py-5 w-100">
    <div class="row">
      <h3 class="text-center fw-bold col-lg-6 col-sm-12 mx-auto"></h3>
      <uploader
        action="/upload"
        class="col-lg-6 col-sm-12 d-flex align-items-center justify-content-center  text-secondary mx-auto my-5 circle"
        @file-uploaded="onFileUploaded"
      >
        <div v-if="storeUser.avatar" class="circle">
          <img :src="storeUser.avatar.url" alt="">
        </div>
        <div v-else class="circle avatar-fallback">
          <i class="fas fa-user-circle b-block w-100 h-100"></i>
        </div>
        <template #uploaded="dataProps">
          <div class="circle">
            <img :src="dataProps.uploadedData.data.url" alt="">
          </div>
        </template>
      </uploader>
      <validate-form
        @form-submit="onFormSubmit"
        class="mx-auto"
      >
        <label class="mb-1 fw-bold"></label>
        <validate-input
          placeholder=""
          v-model="nameVal"
          type="text"
          :rules="nameRule"
        ></validate-input>
        <label class="mb-1 fw-bold"></label>
        <validate-input
          placeholder=""
          v-model="descVal"
          tag="textarea"
          rows="5"
          :rules="descRule"
        ></validate-input>
        <template #submit>
          <button class="btn btn-primary w-100"></button>
        </template>
      </validate-form>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent, ref, computed, watch } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { GlobalDataProps, AvatarProps, UserProps } from '../store'
import Uploader from '../components/Uploader.vue'
import ValidateForm from '../components/ValidateForm.vue'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import createMessage from '../components/createMessage'

export default defineComponent({
  name: 'EditProfile',
  components: {
    ValidateForm,
    ValidateInput,
    Uploader
  },
  setup () {
    // store
    const store = useStore<GlobalDataProps>()
    const router = useRouter()
    const uploadedData = ref()
    const storeUser = computed<UserProps>(() => store.state.user)
    const descVal = ref(storeUser.value && storeUser.value.description)
    const nameVal = ref(storeUser.value && storeUser.value.nickName)
    watch(storeUser, () => {
      if (storeUser.value) {
        descVal.value = storeUser.value.description
        nameVal.value = storeUser.value.nickName
      }
    })
    // 
    const nameRule: RulesProp = [{ type: 'required', message: '' }]
    const descRule: RulesProp = [{ type: 'required', message: '' }]
    // 
    const onFileUploaded = (newAvatar: AvatarProps) => {
      uploadedData.value = newAvatar
    }
    // 
    const onFormSubmit = (result: boolean) => {
      if (result) {
        let sendData
        if (uploadedData.value) {
          sendData = {
            description: descVal.value,
            nickName: nameVal.value,
            avatar: {
              _id: uploadedData.value.data._id,
              url: uploadedData.value.data.url
            }
          }
        } else {
          sendData = {
            description: descVal.value,
            nickName: nameVal.value
          }
        }

        store.dispatch('editUserProfile', { userId: storeUser.value._id, data: sendData }).then(() => {
          createMessage('2', 'success', 2000)
          setTimeout(() => {
            router.push('/')
          }, 2000)
        })
      }
    }
    return {
      onFormSubmit,
      onFileUploaded,
      descVal,
      nameVal,
      nameRule,
      descRule,
      storeUser
    }
  }
})
</script>
<style lang="scss">
.edit-profile {
  width: 100%;
  height: 100%;
  .avatar-fallback {
    color: #cfcfcf;
  }
  .circle {
    width: 200px;
    height: 200px;
    border-radius: 200px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    border: 1px solid #efefef;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.2s;
    }
    &:hover {
      img {
        transform: scale(1.1);
      }
      &::after {
        content: '';
        font-size: 24px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0,0,0,0.2);
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>
