
import { defineComponent, PropType, ref, watch } from 'vue'
import axios from 'axios'

type UploadStatus = 'ready' | 'loading' | 'success' | 'error'
type CheckFunction = (file: File) => boolean

export default defineComponent({
  name: 'Uploader',
  props: {
    action: {
      type: String,
      required: true
    },
    beforeUpload: {
      type: Function as PropType<CheckFunction>
    },
    uploaded: {
      type: Object
    }
  },
  inheritAttrs: false,
  emits: ['file-uploaded', 'file-uploaded-error', 'file-deleted'],
  setup (props, context) {
    const fileInput = ref<null | HTMLInputElement>(null) // reffileInputDOM
    const fileStatus = ref<UploadStatus>(props.uploaded ? 'success' : 'ready') // 
    const uploadedData = ref(props.uploaded)
    watch(() => props.uploaded, newValue => {
      if (newValue) {
        fileStatus.value = 'success'
        uploadedData.value = newValue
      }
    })
    // button
    const triggerUpload = () => {
      // fileInput
      if (fileInput.value) {
        /* HTMLElementclick */
        fileInput.value.click() // 
      }
    }
    // fileInput
    const handleFileChange = async (e: Event) => {
      const currentTarget = e.target as HTMLInputElement // input
      /* type="file"inputvaluefiles */
      if (currentTarget.files) {
        // 
        const files = Array.from(currentTarget.files) // filesFileListArray.from
        if (props.beforeUpload) {
          // beforeUpload
          const result = props.beforeUpload(files[0]) // 
          if (!result) {
            // resultfalse
            return result // result
          }
        }
        fileStatus.value = 'loading' // 
        const formData = new FormData() // FormDataformdata
        formData.append('file', files[0]) // formdata
        try {
          // 
          const res = await axios.post(props.action, formData, {
            headers: { 'Content-Type': 'multipart/form-data' } // 
          })
          uploadedData.value = res.data
          fileStatus.value = 'success' // 
          context.emit('file-uploaded', res.data) // file-uploaded
          if (fileInput.value) {
            // fileInput
            // fileInput
            fileInput.value.value = ''
          }
        } catch (error) {
          fileStatus.value = 'error' // 
          context.emit('file-uploaded-error', { error }) // file-uploaded-error
          if (fileInput.value) {
            // fileInput
            // fileInput
            fileInput.value.value = ''
          }
        }
      }
    }
    const handleDelete = () => {
      if (uploadedData.value) {
        if (fileInput.value) {
          fileInput.value.value = ''
        }
        uploadedData.value = {}
        context.emit('file-deleted')
      }
    }
    return {
      fileInput,
      triggerUpload,
      fileStatus,
      handleFileChange,
      uploadedData,
      handleDelete
    }
  }
})
