import axios from 'axios'
import { createRouter, createWebHashHistory } from 'vue-router'
import store from './store'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Signup from './views/Signup.vue'
import ColumnDetail from './views/ColumnDetail.vue'
import CreatePost from './views/CreatePost.vue'
import PostDetail from './views/PostDetail.vue'
import EditProfile from './views/EditProfile.vue'
import EditColumn from './views/EditColumn.vue'

const routerHistory = createWebHashHistory()
const router = createRouter({
  history: routerHistory,
  scrollBehavior () {
    return { left: 0, top: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: ''
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      // metameta
      meta: {
        redirectAlreadyLogin: true,
        title: ''
      }
    },
    {
      path: '/column/:id',
      name: 'column',
      component: ColumnDetail
    },
    {
      path: '/create',
      name: 'create',
      component: CreatePost,
      meta: {
        requiredLogin: true,
        title: ''
      }
    },
    {
      path: '/signup',
      name: 'signup',
      component: Signup,
      meta: {
        title: ''
      }
    },
    {
      path: '/posts/:id',
      name: 'posts',
      component: PostDetail
    },
    {
      path: '/edit-profile',
      name: 'edit-profile',
      component: EditProfile,
      meta: {
        requiredLogin: true,
        title: ''
      }
    },
    {
      path: '/edit-column',
      name: 'edit-column',
      component: EditColumn,
      meta: {
        requiredLogin: true,
        title: ''
      }
    }
  ]
})

router.beforeEach(async (to, from, next) => {
  const { token, user } = store.state
  // 
  const { requiredLogin, redirectAlreadyLogin, title } = to.meta
  if (!user.isLogin) {
    if (token) {
      axios.defaults.headers.common.Authorization = `Bearer ${token}`
      try {
        await store.dispatch('fetchCurrentUser')
        if (redirectAlreadyLogin) {
          // redirectAlreadyLoginmeta(/login)
          document.title = ''
          next('/')
        } else {
          if (title) document.title = title as string
          // 
          next()
        }
      } catch (error) {
        // tokentoken
        console.error(error)
        // token
        localStorage.removeItem('token')
        store.commit('logout')
        document.title = ''
        next('login')
      }
    } else {
      if (requiredLogin) {
        // tokenrequiredLoginmeta
        document.title = ''
        next('login')
      } else {
        if (title) document.title = title as string
        next()
      }
    }
  } else {
    if (redirectAlreadyLogin) {
      document.title = ''
      next('/')
    } else {
      if (title) document.title = title as string
      next()
    }
  }
})

export default router
