<template>
  <div class="create-post-page py-5">
    <h4 class="text-center fw-bolder">{{ isEditMode? '': '' }}</h4>
    <uploader
      action="/upload"
      class="d-flex align-items-center justify-content-center bg-light text-secondary col-lg-6 col-sm-12 mx-auto my-4"
      :uploaded="uploadedData"
      :beforeUpload="uploadCheck"
      @file-uploaded="handleFileUploaded"
      >
      <h2></h2>
      <template #loading>
        <div class="d-flex">
          <div class="spinner-border text-secondary mx-3"></div>
          <h2>...</h2>
        </div>
      </template>
      <template #uploaded="dataProps">
        <img :src="dataProps.uploadedData.data.url" alt="">
      </template>
    </uploader>
    <validate-form @form-submit="onFormSubmit">
      <div class="mb-3">
        <label class="form-label fw-bolder"></label>
        <validate-input
          :rules="titleRules"
          v-model="titleVal"
          placeholder=""
          type="text"
        ></validate-input>
      </div>
      <div class="mb-3">
        <label class="form-label fw-bolder"></label>
        <validate-input
          type="text"
          tag="textarea"
          rows="10"
          :rules="contentRules"
          v-model="contentVal"
          placeholder=""
        ></validate-input>
      </div>
      <template #submit>
        <button class="btn btn-primary btn-large">{{ isEditMode ? '' : '' }}</button>
      </template>
    </validate-form>
  </div>
</template>
<script lang="ts">
import { defineComponent, ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import { GlobalDataProps, ResponseType, ImageProps, PostProps } from '../store'
import ValidateForm from '../components/ValidateForm.vue'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import Uploader from '../components/Uploader.vue'
import createMessage from '../components/createMessage'
import { beforeUploadCheck } from '../helpers'
export default defineComponent({
  name: 'CreatePost',
  components: {
    ValidateForm,
    ValidateInput,
    Uploader
  },
  setup () {
    const uploadedData = ref()
    const store = useStore<GlobalDataProps>()
    const router = useRouter()
    const route = useRoute()
    // !!booleanid
    const isEditMode = !!route.query.id
    let imageId = ''
    const titleVal = ref('')
    const titleRules:RulesProp = [
      { type: 'required', message: '' }
    ]
    const contentVal = ref('')
    const contentRules:RulesProp = [
      { type: 'required', message: '' }
    ]
    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) {
        imageId = rawData.data._id
      }
    }
    const onFormSubmit = (result: boolean) => {
      if (result) {
        const { column, _id } = computed(() => store.state.user).value
        if (column) {
          const newPost: PostProps = {
            title: titleVal.value,
            content: contentVal.value,
            column,
            author: _id
          }
          if (imageId) {
            newPost.image = imageId
          }
          const actionName = isEditMode ? 'updatePost' : 'createPost'
          const sendData = isEditMode ? {
            id: route.query.id,
            payload: newPost
          } : newPost

          store.dispatch(actionName, sendData).then(() => {
            createMessage('2', 'success', 2000)
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 2000)
          })
        }
      }
    }
    const uploadCheck = (file: File) => {
      const result = beforeUploadCheck(file, {
        format: ['image/jpeg', 'image/png'],
        size: 1
      })
      const { passed, error } = result
      if (error === 'format') {
        createMessage('JPG/PNG', 'error')
      }
      if (error === 'size') {
        createMessage(' 1MB', 'error')
      }
      return passed
    }

    onMounted(() => {
      if (isEditMode) {
        store.dispatch('fetchPost', route.query.id).then((rawData: ResponseType) => {
          const currentPost = rawData.data
          if (currentPost.image) {
            uploadedData.value = { data: currentPost.image }
          }
          titleVal.value = currentPost.title
          contentVal.value = currentPost.content
        })
      }
    })

    return {
      titleVal,
      titleRules,
      contentVal,
      contentRules,
      onFormSubmit,
      uploadCheck,
      handleFileUploaded,
      uploadedData,
      isEditMode
    }
  }
})
</script>
<style lang="scss">
.submit-area {
  text-align: center;
}
.create-post-page {
  width: 100%;
  height: 100%;
  .file-upload-container {
    height: 200px;
    cursor: pointer;
    border: 1px solid #efefef;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
</style>
